}

#testing-extension .app-controls>* {
  width: 100%;
}

#testing-extension .app-title {
  font-size: 24px;
  display: block;
  text-align: center;
  font-weight: 500;
  margin-bottom: 16px;
}

#testing-extension .app-record-button {
  padding: 10px 10px 10px 30px;
  -ms-flex-item-align: center;
      align-self: center;
  color: #fff;
  background-color: #07b94b;
  cursor: pointer;
  border-radius: 4px;
  position: relative;
}

#testing-extension .app-record-button:before {
  content: "";
  position: absolute;
  left: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  -webkit-animation-name: app-pulse;
  animation-name: app-pulse;
  -webkit-animation-duration: 0.8s;
  animation-duration: 0.8s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

#testing-extension .app-subtitle:before {
  content: "";
  position: absolute;
  left: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
  -webkit-animation-name: app-pulse;
  animation-name: app-pulse;
  -webkit-animation-duration: 0.8s;
  animation-duration: 0.8s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

#testing-extension .app-actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
